.background {
  height: 100%;
  width: 100%;
  background: linear-gradient(to right, #2F80ED, #1CB5E0);
  padding: 10px;
}

.container {
  background-color: aliceblue;
  max-width: 500px;
  margin: auto;
  border-radius: 2.5mm;
  padding: 20px;
  box-shadow:0 0 3px black ;
  
}

.heading {
  font-size: 30px;
  font-weight: bold;
  text-align: center;
  color: rgba(0, 0, 0, 0.8);
  padding: 2px;
  padding-bottom: 0px;
  margin: 20px 0px 5px 0px ;
}

form {
  display: flex;
  align-items: center;
  justify-content: center;
}

input {
  width: 100%;
  margin: 10px 0px ;
  padding: 20px 10px;
  border: 1px solid #ccc;
  transition: 0.2s;
  outline: none;
  font-size: 18px;
}
input:focus {
  border: 1px solid #555;
}
input::placeholder {
  font-size: large;
  opacity: 0.5;
  color: #555;
}

.button {
  border: 1px solid #ccc;
  padding: 15.5px;
  margin-left: 5px;
  background-color: rgba(248, 43, 248, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 4rem;
  font-family:monospace;
  box-shadow:0px 5px 10px rgba(darken(dodgerblue, 40%));
  transition:all.3s;
  cursor:pointer;
  border-bottom:4px solid lighten(gray, 70%)
}
.button:hover{
  background-color: rgb(193, 22, 193);
}


.count {
  text-align: center;
  padding: 2px;
}